 /*to_do_list*/
 .to_do_list{
    border: 5px solid  #020e26;
    border-radius: 50px;
    width: 650px;
    height: auto;
    margin-left: 300px;
    margin-top: 100px;
    padding-left: 30px;
    background-image: url(/src/img/mer.jpg);
    background-size: cover;
    background-repeat: no-repeat;
 }
 .container{
    height: auto;
 }
 li{
    list-style: none;
   
 }
 /*H1*/
h1{
    padding-left: 20px;
    margin-left: 130px;
    margin-bottom: 50px; 
    background: linear-gradient(90deg,#c1bdc4,#e4aa9c);
    margin-right: 250px;
}
 
 
 /*supprimer*/
 .suppe {
    font-size: 20px;
    color: #020e26;
    background-color: transparent;
    border: 1px solid #020e26;
    border-radius: 48px;
    padding:  7px 32px;
    transition: 0.4s;
    margin-left: 20px;
}
.suppe:hover{
    color: #fff;
}
.suppe::before{
    content: "";
    position: absolute;
    height: 45px;
    width: 160px;
    z-index: -1;
    border-radius: 48px;
    background-color: #c1bdc4;
    transform: translate(-24px,-8px);
    transition: 0.4s;
}
.suppe:hover::before{
    background-color: #e4aa9c;
    transform: translate(-33px,-13px);
}

/* done */
.done{
    position: absolute;
    border: none;
    width: 9em;
    height: 3em;
    line-height: 2em;
    text-align: center;
    background: linear-gradient(90deg,#c1bdc4,#e4aa9c);
    background-size: 300%;
    border-radius: 30px;
    text-transform: uppercase;
    cursor: pointer;
    z-index: 1;
    margin-left: 20px;
}
.done:hover{
    animation:animation 8s linear infinite;
    border: none;
}
@keyframes animation{
    0%{background-position: 0%;}
    100%{background-position: 400%;}
}
.done:before{
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: -1;
    background: linear-gradient(90deg,#c1bdc4,#e4aa9c,#8a4433);
    background-size: 400%;
    border-radius: 35px;
    transition: 1s;
}
.done:hover::before{
    filter: blur(20px);
}

